import React, { useState } from 'react';
import { NinjaInfo } from './util';
// sub components
import ToolBar from './ToolBar';
import NinjaSquadContainer from './NinjaSquadContainer';
import NinjaCard from './NinjaCard';

// component local
import './NinjaProfiles.css';

interface NinjaProfilesProps {
  ninja: NinjaInfo[];
}

function NinjaProfiles(props: NinjaProfilesProps) {
  const [offices, setOffices] = useState(new Set());

  const ninjaCards = props.ninja
    .filter(e => offices.size === 0 || offices.has(e.office.toLowerCase()))
    .map((e, i) =>
      <NinjaCard  key = {i} 
                  name = {e.name} 
                  imagePortraitUrl = {e.imagePortraitUrl}
                  office = {e.office}
                  linkedIn = {e.linkedIn}
                  gitHub = {e.gitHub}
                  twitter = {e.twitter}
                  />
    );

  const officeOptions: Set<string> = new Set();
  props.ninja.forEach(n => officeOptions.add(n.office.toLowerCase()));

  return (
    <article className="ninja-profiles">
      <header>
        <h1>The fellowship of the tretton37</h1>
        <ToolBar officeOptions={officeOptions} onOfficeChange={(e:Set<string>) => setOffices(new Set(e))}/>
      </header>
      <NinjaSquadContainer>
        {ninjaCards}
      </NinjaSquadContainer>
    </article>
  );
}

export default NinjaProfiles;