import { getOfficeFlag } from './util';
import './ToolBar.css';

interface ToolBarProps {
  officeOptions: Set<string>;
  onOfficeChange: (a: Set<string>) => void;
}

const offices: Set<string> = new Set(); // not really Reacty way. React purists will complain? "just remove the duck" for peer review ;-)

function ToolBar(props: ToolBarProps) {
  // event delegation. checkbox events are collected at a <form> level
  function onOfficeChange(e: React.FormEvent<HTMLFormElement>) {
    const target = e.target as HTMLInputElement;
    const city = target.getAttribute('name');

    // No pattern matching? still? It's 2021 already. Come on Anders, we know you can do it!
    if (!city) return;

    if (target.checked)
      offices.add(city);
    else
      offices.delete(city);

    props.onOfficeChange(offices);
  }

  // Component is using the "Checkbox hack" to toggle the state
  const officeInputs = Array.from(props.officeOptions).map((office, i) => {
    return (
      <div key={i}>
        <input type="checkbox" name={office} id={office}/>
        <label htmlFor={office}><span>{getOfficeFlag(office)}</span> {office}</label>
      </div>
    );
  });

  return (
    <nav className="toolbar">
      <form onChange={onOfficeChange}>
        {officeInputs}
      </form>
    </nav>
  );
}

export default ToolBar;
